.fader {
  width: 80%;
  height: 100%;
  position: relative;
  -webkit-animation: moving 2s;
  animation: moving 2s;

}

// ---- Fading browser support ----- //
@-webkit-keyframes moving {
  from {
    bottom: -80px;
  }

  to {
    bottom: 0px;
  }
}

@keyframes moving {
  from {
    bottom: -80px;
  }

  to {
    bottom: 0px;
  }
}

// ---- Fading browser support ----- //


.faderSlow {
  -webkit-animation: fadein 4s;
  /* Safari, Chrome and Opera > 12.1 */
  -moz-animation: fadein 4s;
  /* Firefox < 16 */
  -ms-animation: fadein 4s;
  /* Internet Explorer */
  -o-animation: fadein 4s;
  /* Opera < 12.1 */
  animation: fadein 4s;
}

// ----- vv Animation Browser support vv --------//
@keyframes fadein {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

/* Firefox < 16 */
@-moz-keyframes fadein {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

/* Safari, Chrome and Opera > 12.1 */
@-webkit-keyframes fadein {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

/* Internet Explorer */
@-ms-keyframes fadein {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

