/* Card with shadow */
.card {
  outline-offset: 12px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
  transition: 0.3s;
  /* Rounded corners */
  border-radius: 5px;
  margin: 20px;
  /* Add background color to the cards to make them white. */
  background-color: #323444f5;
  width: 500px;
  color: #FFF;

  a {
    color: rgb(255, 145, 0);
    font-size: large;
  }
}

.card:hover {
  /* When hover, change shadow... cool! */
  box-shadow: 0 50px 160px 0 rgba(0, 0, 0, 0.2);
}

.card img {
  /* Rounded corners */
  border-radius: 5px 5px 0 0;
  width: 100%;
}

